<script>
import dayjs from 'dayjs'
export default {
  filters:{
    date(val){
      return dayjs(val).format('MM/DD')
    }
  },
  data(){
    return {
        swiperOptions: {
          pagination: {
            el: '.pagination-home'
          },
          // Some Swiper option/callback...
        },
        newsCats:[],
        heroCats:[]
      }
  },
  methods:{
    //获取新闻分类的数据
    async fetchNewsCats(){
      const res=await this.$http.get('news/list')
      this.newsCats=res.data
    },
    //获取英雄分类的数据
    async fetchHeroCats(){
      const res=await this.$http.get('heroes/list')
      this.heroCats=res.data
    }
  },
  //组件初始化结束，还未开始渲染
  created(){
    this.fetchNewsCats()
    this.fetchHeroCats()
  }
}
</script>

